#grid {
	border-collapse: collapse;
	margin-left: 5px;
	margin-right: 5px;
}

#grid td {
	width: 25px;
	height: 25px;
	border-color: cornflowerblue;
}

table {
	border-spacing: 0;
	border-style: hidden;
}

.unvisited {
	border: 1px solid rgb(0, 0, 0);
	background-color: white;
}

.visited {
	border: 1px solid rgb(0, 0, 0);
	animation: animateVisited;
	animation-duration: 1s;
	animation-iteration-count: 1;
	animation-fill-mode: forwards;
	animation-timing-function: ease-in-out;
}

@keyframes animateVisited {
	0% {
		background-color: white;
		opacity: 0.3;
	}
	25% {
		background-color: lightblue;
		opacity: 0.5;
	}
	50% {
		background-color: lightskyblue;
		opacity: 0.6;
	}
	100% {
		background-color: rgb(52, 16, 212);
		opacity: 0.75;
	}
}

.start {
	border: 1px solid rgb(0, 0, 0);
	background-color: rgb(26, 204, 56);
	animation: nodePlaced;
	animation-duration: 500ms;
	animation-timing-function: ease-in-out;
	animation-fill-mode: forwards;
}

.end {
	border: 1px solid rgb(0, 0, 0);
	background-color: rgb(253, 0, 0);
	animation: nodePlaced;
	animation-duration: 500ms;
	animation-timing-function: ease-in-out;
	animation-fill-mode: forwards;
}

.wall {
	border: 1px solid rgb(0, 0, 0);
	background-color: rgb(0, 0, 0);
	animation: nodePlaced;
	animation-duration: 500ms;
	animation-timing-function: ease-in-out;
	animation-fill-mode: forwards;
}

.shortestPath {
	border: 1px solid rgb(0, 0, 0);
	background-color: rgb(149, 194, 27);
	animation: animateSP;
	animation-duration: 0.2s;
	animation-iteration-count: 1;
	animation-fill-mode: forwards;
}

@keyframes nodePlaced {
	0% {
		opacity: 0.5;
	}
	50% {
		opacity: 0.7;
	}
	100% {
		opacity: 0.9;
	}
}

@keyframes animateSP {
	0% {
		background-color: white;
		opacity: 0.3;
	}
	25% {
		background-color: rgb(155, 154, 89);
		opacity: 0.4;
	}
	50% {
		background-color: rgb(148, 146, 46);
		opacity: 0.5;
	}
	100% {
		background-color: rgb(233, 236, 8);
		opacity: 0.65;
	}
}
